<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Categoría > Subcategoría > Confirmar datos</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ operation.document_name }}</div>

          <div class="call-to-action">
            <v-row v-if="isReadOnly" style="background-color: #184C72; color: white">
              <v-col class="text-left">Estado: {{ operation.status.name }}</v-col>
              <v-col class="text-right">{{ operation.created_at | dateTime }}</v-col>
            </v-row>
            <v-row>
              <v-col>
                <div class="text text-center" style="display: block">
                  <div v-if="!operation.variable_price">Costo del documento : S/. {{ operation.price }}</div>
                  <div>Código de solicitud: {{ operation.id | padleft }}</div>
                  <div>Abogado responsable: LegalHelp</div>
                </div>
              </v-col>
            </v-row>
          </div>

          <v-row>
            <v-col cols="4" align-self="center">Departamento:</v-col>
            <v-col cols="8">{{ operation.ubigeo_department.name }}</v-col>
          </v-row>

          <v-row v-for="field in operationFormData" :key="field.id">
            <v-col cols="4" align-self="center">{{ field.label }}:</v-col>
            <v-col cols="8">{{ field.value instanceof Object ? field.value.text : field.value }}</v-col>
          </v-row>

          <div v-if="!isReadOnly" class="text-center mt-5">
            <v-btn color="secondary" block @click="goToData()">
              Corregir Datos
            </v-btn>
          </div>
          <div v-if="!isReadOnly" class="text-center mt-2">
            <v-btn color="primary" block @click="goToComplete()">
              Confirmar
            </v-btn>
          </div>

          <v-row>
            <v-col class="text-center">
              <v-btn v-if="operation.hasDocument" @click="goToDocument"
                     :href="operation.documentUrl" target="_blank"
                     color="secondary" block>
                Documento final
              </v-btn>
            </v-col>
          </v-row>

          <div v-if="isReadOnly" class="text-center mt-5">
            <v-btn color="primary" block @click="goToOperations()">
              Ir a tus tramites
            </v-btn>
          </div>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import axios from "axios";

export default {
  name: 'DocumentsDataConfirm',
  data: () => ({
    isLoading: false,
    operation: {
      lawyer_specialty_id: {},
      ubigeo_department_id: {},
      ubigeo_department: {},
      document: '',
      lawyer: '',
      status: {}
    },
    operationFormData: ''
  }),
  computed: {
    ...mapGetters(['user']),
    operationId() {
      return this.$route.params.id
    },
    isReadOnly() {
      return this.$route.query.readOnly
    }
  },
  mounted() {
    this.loadOperation()
  },
  methods: {
    loadOperation() {
      axios.get('operations/' + this.operationId).then((response) => {
        this.operation = response.data
        this.loadFormData()
      })
    },
    loadFormData() {
      let url = 'external-resources-forms/' + this.operation.resource_id + '?operation_id=' + this.operation.id
      axios.get(url).then((response) => {
        this.operationFormData = response.data
      }).catch().then(() => {

      })
    },
    goToData() {
      this.$router.push({name: 'DocumentsData', params: {id: this.operation.id}})
    },
    goToComplete() {
      // save data
      this.isLoading = true

      let data = {status_id: this.operation.document.variable_price ? 2 : 7};
      let nextPage = this.operation.document.variable_price ? 'DocumentsDataValidate' : 'DocumentsDataComplete';

      axios.put('operations/' + this.operation.id, data).then(() => {
        this.$router.push({name: nextPage, params: {id: this.operation.id}});
      }).catch(() => {
      }).then(() => {
        this.isLoading = false
      })
    },
    goToOperations() {
      this.$router.push({name: 'OperationIndex'})
    }
  }
}
</script>
